import { useState, React } from 'react';
import './ExperienceTab.css';

function ExperienceTab() {

    const [activeTab, setActiveTab] = useState(1);

    return (
        <div className="experience-tab">
            <h1 className="tab-title"> Experience </h1>
            <div className="tab-container">
                <div className="bloc-tabs">
                    <button className={activeTab === 1 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(1)}>SPIRIT OF MATH</button>
                    <button className={activeTab === 2 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(2)}>CUMMER LODGE</button>
                    <button className={activeTab === 3 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(3)}>ROBOTICS</button>
                    <button className={activeTab === 4 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(4)}>MCPT</button>
                    <button className={activeTab === 5 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(5)}>MSC</button>
                    <button className={activeTab === 6 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(6)}>ATHL COUNCIL</button>
                    <button className={activeTab === 7 ? "tabs active-tab" : "tabs"} onClick={() => setActiveTab(7)}>DECA</button>
                </div>

                <div className="content-tabs">
                    <div className={activeTab === 1 ? "content active-content" : "content"}>
                        <h2> Assistant Teacher <span style={{color: "#4bfac8"}}> @ Spirit Of Math</span></h2>
                        <h3> October 2019 - June 2020 </h3>
                        <hr/>
                        <p> - Worked with a teacher to execute lesson plans </p>
                        <p> - Assisted with marking and teaching younger students in mathematics </p>
                        <p> - Helped run math drills during class </p>
                    </div>
                    <div className={activeTab === 2 ? "content active-content" : "content"}>
                        <h2> Volunteer <span style={{color: "#4bfac8"}}> @ Cummer Lodge Retirement Home</span> </h2>
                        <h3> November 2019 - Present </h3>
                        <hr/>
                        <p style={{color: "#FFF"}}> PAUSED DUE TO COVID RESTRICTIONS </p>
                        <p> - Helped out with errands in the residence </p>
                        <p> - Assisted senior citizens with their activities </p>
                        <p> - Visited and talked with residents </p>
                    </div>
                    <div className={activeTab === 3 ? "content active-content" : "content"}>
                        <h2> Robotics Volunteer <span style={{color: "#4bfac8"}}> @ JB Tyrrell SPS</span> </h2>
                        <h3> December 2018 </h3>
                        <hr/>
                        <p> - Shared previous experience in FLL competition </p>
                        <p> - Provided advice and guidance to current team </p>
                    </div>
                    <div className={activeTab === 4 ? "content active-content" : "content"}>
                        <h2> Mackenzie Computer Programming Team</h2>
                        <h3> 2020 - 2021 </h3>
                        <hr/>
                        <p> - Executive of the Competitive Branch for MCPT </p>
                        <p> - Helped set CP problems for contests with participation from 75+ teams globally </p>
                        <p> - Wrote and taught lessons on various algorithms and data structures </p>
                    </div>
                    <div className={activeTab === 5 ? "content active-content" : "content"}>
                        <h2> Mackenzie Science Club</h2>
                        <h3> 2020-2021 </h3>
                        <hr/>
                        <p> - Executive of MSC </p>
                        <p> - Helped design and run events for the largest school-wide club </p>
                        <p> - Assisted with setup of the MSEO, an annual STEM competition held for middle schools </p>
                        <p> - Designed logo for apparel </p>
                    </div>
                    <div className={activeTab === 6 ? "content active-content" : "content"}>
                        <h2> Mackenzie Athletic Council</h2>
                        <h3> 2019-2021 </h3>
                        <hr/>
                        <p> - Graphic Designer for MAC </p>
                        <p> - Created posters and infographics using Inkscape and Photoshop </p>
                        <p> - Helped run health and wellness initatives </p>
                        <p> - Assisted in designing and selling apparel </p>
                    </div>
                    <div className={activeTab === 7 ? "content active-content" : "content"}>
                        <h2> DECA</h2>
                        <h3> 2019-2021 </h3>
                        <hr/>
                        <p> - Competed in Business Admin and Marketing categories </p>
                        <p> - Completed case studies and exams, placing in both provincials and regionals</p>
                        <p> - SEM Category Provincials Finalist in 2021 </p>
                        
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ExperienceTab;
