import React from 'react';
import CardItem from './CardItem';
import './Cards.css';

function Cards() {
    return (
        <div className='cards'>
            <h1>MY PROJECTS</h1>
            <div className='cards__container'>
                <div className='cards__wrapper'>
                    <div className = 'double__cards'>
                        <ul className='cards__items'>
                            <CardItem 
                                src='../images/cp_thumb.png'
                                text='Competitive Programming'
                                labels={['PYTHON', 'C++']}
                                path='https://github.com/KennethRuan/competitive-programming'
                                description='A repository of solutions for competitive programming problems.'
                            />
                            <CardItem 
                                src='../images/sigma_thumb.png'
                                text='Sigma NLP'
                                labels={['PYTHON', 'TENSORFLOW']}
                                path='https://github.com/RonaldYou/Sigma'
                                description='A streamlined NLP sentiment analyzer built with Tensorflow. Won third place at IgnitionHacks.'
                            />
                        </ul>
                    </div>
                    <div className="triple__cards">
                        <ul className='cards__items'>
                            
                            <CardItem 
                                src='../images/velo_thumb.png'
                                text='Velo'
                                labels={['PYTHON', 'FLASK']}
                                path='/https://github.com/skyflaren/velo'
                                description='An automated travel plan generator. Uses machine learning techniques to create an optimal pathing for vacation itineraries.'
                            />
                            <CardItem 
                                src='../images/trendit_thumb.jpg'
                                text='Trendit'
                                labels={['PYTHON', 'FLASK']}
                                path='https://github.com/skyflaren/trendit'
                                description='A processing engine that returns summaries of the largest events for given keywords.'
                            />
                            <CardItem 
                                src='../images/conductor_thumb.png'
                                text='Conductor'
                                labels={['PYTHON', 'OPENCV']}
                                path='https://github.com/KennethRuan/Conductor'
                                description='A program that maps hand gestures and movement to allow hands-free computer browsing.'
                            />
                        </ul>
                    </div>
                    <div className="triple__cards">
                        <ul className='cards__items'>
                            <CardItem 
                                src='../images/atc_thumb.png'
                                text='At The Counter'
                                labels={['JAVA']}
                                path='https://github.com/skyflaren/atTheCounter'
                                description='An endless scroller game that aims to educate about Coronavirus. Created for ICS4U0 ISP.'
                            />
                            <CardItem 
                                src='../images/ttt_thumb.png'
                                text='TicTacToe 3D'
                                labels={['JAVA']}
                                path='https://github.com/KennethRuan/TicTacToe-3D'
                                description='A 3D twist on the classic game. Created for ICS3U3 ISP.'
                            />
                            <CardItem 
                                src='../images/mousemaze_thumb.png'
                                text='Mouse Maze'
                                labels={['PROCESSING']}
                                path='https://github.com/KennethRuan/MouseMaze'
                                description='Navigate three mazes as a mouse. Created for ICS2U3 ISP.'
                            />
                            
                        </ul>
                    </div>
                </div>
            </div>
            <div class="cursor"/>
        </div>
    );
}

export default Cards