import React from 'react';
import '../../App.css';
import Footer from '../Footer';
import GalleryView from '../GalleryView';
import { SliderData } from '../SliderData';

function GalleryPage (){
    return(
        <>
            <GalleryView slides={SliderData}/>
            <Footer />
        </>
    );
}

export default GalleryPage;