import React from 'react';
import { Link } from 'react-router-dom';
import './Footer.css';

function Footer() {
    return (
        <div className='footer-container'>
            <section className="social-media">
                <div className="social-media-wrap">
                    <div className="social-icons">
                        <Link className="social-icon-link github"
                            to={{ pathname: 'https://github.com/KennethRuan'}}
                            target="_blank"
                            aria-label="Github">
                            <img className="social-icon github" alt="Github Logo" src="../images/github-icon.png"/>
                        </Link>
                        <Link className="social-icon-link devpost"
                            to={{pathname: "https://devpost.com/KennethRuan"}}
                            target="_blank"
                            aria-label="Devpost">
                            <img className="social-icon devpost" alt="Devpost Logo" src="../images/devpost-icon.png"/>
                        </Link>
                        <Link className="social-icon-link linkedin"
                            to={{pathname: "https://www.linkedin.com/in/kenneth-ruan-4107651a1/"}}
                            target="_blank"
                            aria-label="Devpost">
                            <img className="social-icon linkedin" alt="Linkedin Logo" src="../images/linkedin-icon.png"/>
                        </Link>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Footer
