import React, {useState} from 'react';
import './GalleryView.css';
import { SliderData } from './SliderData';

function GalleryView({slides}) {

    const [current, setCurrent] = useState(0);
    const length = slides.length;

    console.log(length);

    const nextSlide = () => {
        setCurrent(current === length - 1 ? 0 : current + 1)
    }
    const prevSlide = () => {
        setCurrent(current === 0 ? length - 1 : current - 1)
    }

    return (
        <div className="gallery-view">
            <div className="headline">
                <h1>GRAPHIC DESIGN</h1>
                <p>PORTFOLIO</p>
            </div>
            <div className="gd-wrapper">
                <div className="left-arrow" onClick={prevSlide}><p>{"<"}</p></div>
                <div className="gd-container">
                    {SliderData.map((slide, index) =>{
                        return (
                            <div className={index === current ? 'slide active' : 'slide'} key={index}>
                                {index === current && (<img src={slide.image} alt="Missing" className="gd-image"/>)}
                            </div>
                        )
                    })}
                </div>
                <div className="right-arrow" onClick={nextSlide}><p>{">"}</p></div>
            </div>
            <div className="cursor"/>
        </div>
    )
}
export default GalleryView

