import React from 'react';
import { Link } from 'react-router-dom';

function CardItem(props) {
    var tags = [];
    for (var i = 0; i < props.labels.length; i++){
        tags.push(<div className="cards__tag" data-category={props.labels[i]} />)
    }

    return (
        <>
            <div className="cards__item">
                <Link className="cards__item__link" to={{pathname: props.path}} target="_blank">
                    <figure className="cards__item__pic-wrap">
                        <p> {props.description} </p>
                        <img src={props.src} alt="Missing" className="cards__item__img"/>
                        <div className="cards__tags">
                            {tags}
                        </div>
                    </figure>
                    <div className="cards__item__info">
                        <h5 className="cards__item__text">
                            {props.text}
                        </h5>
                    </div>
                </Link>
            </div>
        </>
    )
}

export default CardItem
